/* The device with borders */
.smartphone {
    @include light-shadow();
    position: relative;
 //   width: 10px;
 //   height: 430px;
    margin: auto;
    border: 16px white solid;
    border-top-width: 60px;
    border-bottom-width: 60px;
    border-radius: 36px;
    width: 240px;
  }
  
  /* The horizontal line on the top of the device */
  .smartphone:before {
    content: '';
    display: block;
    width: 60px;
    height: 5px;
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    border-radius: 10px;
    border: 1px solid #666;

  }
  
  /* The circle on the bottom of the device */
  .smartphone:after {
    content: '';
    display: block;
    width: 35px;
    height: 35px;
    position: absolute;
    left: 50%;
    bottom: -65px;
    transform: translate(-50%, -50%);
    background: white;
    border-radius: 50%;
    border: 1px solid #666;
  }
  
  /* The screen (or content) of the device */
  .smartphone .content {
  //  width: 360px;
   // height: 640px;
    background: white;
  }

  .smartphone img {
    border-radius: 8px;
  }