.annotation {
    @include no-user-select();
      position: absolute;
      visibility: hidden;
      top: 0;
      left: 0;
      z-index: 1;
      margin-left: 0px;
      margin-top: 0px;
      padding: 1em;
      width: 200px;
      color: #fff;
      background: rgba(0, 0, 0, 0.8);
      border-radius: .5em;
      font-size: 12px;
      line-height: 1.2;
      transition: opacity .5s;
      overflow-x: hidden;
      overflow-wrap: break-word;
      white-space: pre-wrap;
    }
    