.feedback-form {
    display: flex;
    justify-content: center;
    align-items: center;
    height: inherit;
    padding: 3px;

    form {
        width: 100%;
        padding: 20px;
        background: #fff;
        box-shadow: 0 2px 5px #ccc;
    }

    input {
        width: calc(100% - 10px);
        padding: 5px;
        border: 1px solid #ccc;
        border-radius: 3px;
        vertical-align: middle;
    }

    input:hover, textarea:hover {
        outline: none;
        border: 1px solid #095484;
    }

    .first-name {
        margin-bottom: 22px;
    }

    span {
        color: red;
    }

    th,td {
        padding: 15px 0;
        border-bottom: 1px solid #ccc;
        text-align: center;
        vertical-align: unset;
        line-height: 18px;
        font-weight: 400;
        word-break: break-all;
        background-color: white;
        border: none;
    }

    table {
        -webkit-box-shadow: none;
        box-shadow: none;
        border: none;
    }

    .first-col {
        width: 16%;
        text-align: left;
    }

    table {
        width: 100%;
    }

    textarea {
        width: calc(100% - 6px);
    }

    .btn-block {
        margin-top: 20px;
        text-align: center;
    }

    button {
        width: 150px;
        padding: 10px;
        border: none;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        background-color: #095484;
        font-size: 16px;
        color: #fff;
        cursor: pointer;
    }

    button:hover {
        background-color: #0666a3;
    }

    @media (min-width: 568px) {

        th,
        td {
            word-break: keep-all;
        }
    }
}