.homepage {
  @extend .fullwidth-page;
  background-color: white;
}

.full-width-header {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding-bottom: 43%;
  display: flex;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
  height: 700px;
}

.full-width-header img {
  opacity: 0.9;
}

.full-width-header-text {
  color: black;
  max-width: 800px;
  text-align: center;
}

.full-width-header-promo-button {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%, 0);
}

.homepage-section-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.homepage-section {
  @extend .fullwidth-section;
  align-content: center;
  max-width: 1200px;
  width: 100%;
  flex-wrap: wrap;
  font-family: Montserrat;
  padding: 10px;
}

.homepage-section .flexrow {
  align-items: stretch !important;
}

.homepage-section .flexrow {
  align-items: stretch !important;
}

.homepage-section .flexcol {
  justify-content: center;
}

.homepage-section h1 {
  text-align: center;
}

.homepage-section q {
  font-weight: 400;
}

.homepage-section-img {
  margin: 20px auto;
  display: block;
  max-width: 500px;
  width: 100%;
  border-radius: 10px;
  opacity: 0.9;
}

.homepage-section iframe {
  display: block;
  margin: 0 auto;
  border: none;
  width: 100%;
  height: 100%;
}

.homepage-video {
  width: 100%;
}

.homepage-viewer {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}

.homepage .flexcol:first-child {
  padding-right: 5%;
  flex: 1;
}

.homepage .flexcol:last-child {
  flex: 2;
}

.homepage-images-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 800px;
  margin: auto;
}

.homepage-detail-container {
  display: flex;
  flex-wrap: wrap;
  margin: 20px auto;
  justify-content: center;
  text-align: left !important;
  max-width: 800px;
  align-items: center;
}

.homepage-detail-container>div {
  text-align: left !important;
  padding: 15px;
  flex: 1;
  min-width: 300px;
}

.homepage-detail-container img {
  max-width: 400px;
  width: 100%;
}

.homepage-detail-container q {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500 !important;
}

.homepage-button-base {
  background-color: $primarycol;
  border: none;
  color: white !important;
  font-weight: bold;
  padding: 8px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 20px;
  margin: 20px 0;
}

.homepage-button {
  @extend .homepage-button-base;
}

.homepage-button-medium {
  @extend .homepage-button-base;
}

.homepage-button-large {
  @extend .homepage-button-base;
}

.homepage-button-large a {
  color: white;
}

.homepage-button-medium a {
  color: white;
}

.home-page-feature-img {
  @include light-shadow();
  max-width: 1200px;
  width: 100%;
  margin: 20px auto;
  border-radius: 20px;
}

.homepage-feature-icon {
  width: 80px !important;
  margin-bottom: 15px;
}

.homepage-configurator-aspect {
  @include aspect-ratio(3, 2);
}

@media only screen and (max-width: 600px) {
  .homepage-configurator-aspect {
    @include aspect-ratio(2, 3);
  }
}

.feature-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-auto-rows: minmax(min-content, max-content);
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  font-size: 0.9rem;
  margin-top: 20px;
  max-width: 1024px;
  width: 100%;
}

.feature-container h4 {
  font-weight: 700;
  margin-top: 25px !important;
  margin-bottom: 20px !important;
}

.feature-container>div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  background-color: #f8f8f8;
  border-radius: 10px;
}

.feature-container img {
  height: 60px;
  margin-left: auto;
  opacity: 0.8;
}

.feature-container img.medium {
  height: 180px;
  margin-left: auto;
  opacity: 0.8;
}

.image-frame-circle {
  @include light-shadow();
  border-radius: 50%;
  border: solid 7px #ddd;
  max-width: 200px !important;
  width: 20%;
}

.slider img {
  border-radius: 10px;
}

.large-feature-box {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}

.large-feature-box>div {
  text-align: left !important;
  max-width: 400px;
  flex: 1 1;
  padding: 5px;
  min-width: 200px;
}

.product-features-row {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  justify-content: space-evenly;
  min-height: 350px;  
}

.product-features-box {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: Montserrat;
}

.product-features-box div:first-child {
  flex: 1;
  padding: 0 2%;
  min-width: 280px;
}

.product-features-box div:last-child {
  flex: 1;
  padding: 0 2%;
}

.product-features-box img {
  width: 300px;
  opacity: 0.95;
}

.product-features-box-builder-image {
  width: 150px;
}

.full-width-viewer-header {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  background: rgb(250,250,250);
  background: linear-gradient(0deg, rgba(250,250,250,1) 0%, rgba(163,163,205,1) 89%);
}

.full-width-viewer-header div:first-child {
  flex: 2;
  padding-left: 20px;
}

.full-width-viewer-header div:last-child {
  flex: 3;
  min-width: 300px;
}

.full-width-viewer-header div.viewer-outer {
  @include aspect-ratio(3,2);
}

@media only screen and (max-width: 768px) {  
  .full-width-viewer-header div.viewer-outer {
    @include aspect-ratio(1,1);

  }
}