.ezv-center-both {
  position: relative;
  left: 50%;
  top: 50%;
  margin-right: -50%;
  margin-bottom: -50%;
  transform: translate(-50%, -50%);
}

.play-button {
    width: 55px;
    cursor: pointer;
}

.play-button-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  padding: 0;
  background-color: transparent;
}

.progress-container {
    width: 200px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font: 18px Arial, sans-serif;
}

.progress-bar {
    width: 0%;
    height: 100%;
    background-color: #fff;
    -webkit-transition: width .6s ease;
    -o-transition: width .6s ease;
    transition: width .6s ease;
    overflow: visible;
}

.progress {
    height: 3px;
    margin-bottom: 4px;
    overflow: hidden;
    border: 2px solid #444;
    border-radius: 4px;
    background-color: #333;
}

.white-outlined-text {
    color: white;
    text-shadow: 0 0 1px black, 0 0 1px black, 0 0 1px black, 0 0 1px black;
}
.ezv-options-menu-bar {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 5px;
    bottom: 38px;
    opacity: 0.8;
  }
  
  .ezv-main-menu-bar {
    display: flex;
    flex-direction: row;
    position: absolute;
    right: 5px;
    bottom: 5px;
    opacity: 0.8;
  }
  
  .ezv-open-menu-bar {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 5px;
    bottom: 5px;
  }
  
  .ezv-help-window {
    position: absolute;
    background-color: rgba(128, 128, 128, 0.8);
    color: white;
    max-width: 420px;
    border: 1px solid #888;
    border-radius: 10px;
    z-index: 2000;
    padding: 8px 12px 8px 12px;
    width: 90%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  
  .ezv-help-window-header {
    font-size: larger;
    display: flex;
    border-bottom: 1px solid #444;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
  }
  
  .ezv-sound-menu-bar {
    position: absolute;
    top: 10px;
    right: 10px;
  }
  
  .ezv-menu-bar-button {
    border-radius: 4px;
    border: 1px solid #999;
    background-color: rgba(255, 255, 255, 0.9);
    margin: 1px;
    padding: 2px;
  }
  
  button.ezv-menu-bar-button img {
      width: 24px;
      height: 24px;
      padding: 1px;
      display: block;
      margin: auto;
      opacity: 75%;
  }

  .ezv-info-window {
    position: absolute;
    max-width: 500px;
    width: 90%;
    background-color: rgba(128, 128, 128, 0.5);
    color: white;
    border: 1px solid #888;
    border-radius: 10px;
    z-index: 2000;
    padding: 8px 12px 8px 12px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  
  .ezv-info-header {
    font-size: larger;
    display: flex;
    border-bottom: 1px solid #444;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .ezv-info-body {
    text-align: left;
    overflow-x: auto;
    overflow-y: auto;
    scrollbar-width: none;
    /* Firefox */
    -ms-overflow-style: none;
    white-space: pre-line;
    /* Internet Explorer 10+ */
  }

  .xr-button-container {
    display: flex;
    position: absolute;
    left: 50%;
    top: 15px;
    transform: translate(-50%, 0);
 }

.xr-button {
    padding: 4px 8px;
    border: 1px solid #aaaa;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.25);
    z-index: 10000;
    align-self: center;
    margin-right: 3px;
}
  
.xr-button img {
    height: 18px;
    vertical-align: middle;
}
  
.xr-button a {
    font-size: 12px;
    color: #4285F4;
    text-decoration: none;
    margin-left: 4px;
    position: relative;
}
  
.ar-iframe-button {
    width: fit-content;
    padding-top: 4px;
    padding-bottom: 5px;
    padding-left: 8px;
    padding-right: 11px;
    border: 1px solid #aaaa;
    border-radius: 10px;
    vertical-align: middle;
    background-color: rgba(255, 255, 255, 0.5);
}
  
.ar-iframe-button img {
    height: 18px;
    vertical-align: middle;
}
  
.ar-iframe-button a {
    font-size: 12px;
    color: #4285F4;
    text-decoration: none;
}
  
.ar-close-button {
    position: absolute;
    right: 10px;
    top: 10px; 
    width: 24px;
    z-index: 2000;
}