.sliding-select {
    padding: 10px;
    display: flex;
    max-width: 350px;
}

.sliding-select-button {
    font-size: 1.4rem;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.sliding-select-content {
    @include no-user-select;
    display: flex;
    font-size: 1.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    white-space:nowrap;
    margin-left: 20px;
    margin-right: 20px;
}
