
.sliding-side-panel {
    width: 320px;
    z-index: 4;
    @include transition();
    position: absolute;
    height: 100%;
    border-right: 1px solid #888;
    cursor: pointer;
  }
  
  #sliding-side-panel-right { 
    @extend .sliding-side-panel;
    top: 0;
    right: 0px;
  }
  
@media screen and (min-width: 768px) {
  #sliding-side-panel-right {
//    position: static;
//    right: 0;
    width: 320px;
    background-color: $panel-background
  }
  .material-editor-container .MuiPaper-root {
    background-color: $panel-background;
  } 
  
  .sliding-side-panel-close-right {
    right: -100px;
  }
}

@media screen and (max-width: 768px) {
  #sliding-side-panel-right {
    position: absolute;
    width: 300px;
    background-color: $panel-background-semitrans;
  }
  .material-editor-container .MuiPaper-root {
    background-color: $panel-background-semitrans;
  } 
}
.sliding-side-panel-tab {
  @include no-user-select();
  top: 2px;
  width: 24px;
  height: 30px;
  border: solid 1px #888;
  background-color: #ccc;
  padding-left: 3px;
  /* padding-top: 5px;
  padding-bottom: 5px; */
  cursor: pointer;
  position: absolute;
}

.sliding-side-panel-tab-right {
  @extend .sliding-side-panel-tab;
  border-radius:  10px 0 0 10px;
  border-left: none;
  left: -24px;
}
.sliding-side-panel-close-right {
  position: absolute;
  right: 16px;
  top: 2px;
}

.sliding-side-panel-icon {
  width: 18px;
  height: 18px;
}

.sliding-side-panel-handle-right {
  @include no-user-select();
  width: 12px;
  height: 100%;
  border: solid 1px #888;
  background: linear-gradient(90deg, #eee, #ccc);
  cursor: pointer;
  position: absolute;
  border-left: none;
  left: -12px;
}

.sliding-side-panel-handle-button {
  height: 50px;
  position: absolute;
  transform: translate(0, -50%);
  top: 50%;
  left: -5px;
}
