/* plan page */
.plan-container {
    display: flex;
    text-align: center;
    flex-wrap: wrap;
    justify-content:space-around;
}
  
.plan-container > div {
    padding: 8px;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
@media screen and (max-width: 767px) {
    .plan-container > div {
      min-width: 100%;
    }
}
 
.plan-signup-button {
    border: 1px solid #888;
    position: relative;
    top: -30px;
    background-color: white;
    padding: 8px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: large;
    border-radius: 10px;
    width: fit-content;
}
  
@media only screen and (max-width: 600px) {
    .plan-outer {
      width: 100%;
    }
}
  
table.plan-table {
    width: 80%;
    border-collapse: collapse;
    border-spacing: 0;
    border: 1px solid #ddd;
    background-color: #ddd;
}
  
table.plan-table td th {
    text-align: center;
    padding: 16px;
    color: #444;
}
  