
.plan-card {
    padding-top: 10px; 
    background-color: white;
    border-radius: 10px;
    color: #444;
    margin-top: 1em;
    margin-bottom: 1em;
    text-align: center;
    padding: 40px 15px;
    flex: 1;
    border: 1px solid #aaa;
    background-color: rgba(240, 147, 25, 0.3);
} 
  
.plan-card h1 {
    font-weight: 700 !important;
} 

.plan-card:hover {
    box-shadow: 0 0 30px $primarycol ;
}
  
.plan-card .price {
    font-size: 55px;
}
  
.plan-card .price-curr {
    position: relative;
    top: -20px;
    font-size: 24px;
}
  
.plan-card .price-footer {
    font-size: 16px;
}
  
.plan-signup-button {
    border: 1px solid #888;
    position: relative;
    top: -30px;
    background-color: white;
    padding: 8px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: large;
    border-radius: 10px;
    width: fit-content;
    font-weight: 700 !important;

}
  