.configurator-panel {
  display: flex;
  flex-direction: column;
  color: #222;
  font-size: small;
  height: 100%;
  align-content: flex-start;
  align-items: center;
  overflow-y: auto;
}

.configurator-title {
  margin-top: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  color: black;
  -webkit-user-select: none;
  user-select: none;
}

.configurator-variant-title {
  @extend .configurator-title;

  font-size: 0.95rem;
  text-align: center;
}

.configurator-part-title {
  @extend .configurator-title;

  font-size: 1.1rem;
  font-weight: 500;
  margin: 5px 0;
}

.configurator-panel-title {
  @include no-user-select();
  font-size: 1.4rem;
  font-weight: 500;
  padding-bottom: 5px;
  white-space: pre-line;
}

.configurator-variant-icons-container {
    display: flex;
    flex-direction: row;
    flex-wrap: none;
    align-items: center;
    overflow-x: scroll;
}

.configurator-variant-icons-container > div {
    margin: 4px 8px;
}

.configurator-variant-icons-container::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

.configurator-parts-view-compact {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: baseline;
}

.configurator-variant-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.configurator-parts-view-expanded {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.configurator-back-button {
  width: 20px;
  display: block;
  margin: auto;
  margin-bottom: 5px;
}

.configurator-variant {
  @include no-user-select;
  //margin-top: 8px;
  font-weight: normal;
  border: 2px solid #888;
  padding: 5px 30px;
  border-radius: 20px;
  font-size: 1.4rem;
  margin-left: 10px;
  margin-right: 10px;
  white-space: nowrap;
}

.configurator-variant-icon {
  width: 25px;
  height: 25px;
  margin: 2px;
  border-radius: 50%;
  border: 1px solid #aaa;
}

.configurator-variant-name {
  white-space: nowrap;
  text-align: center;
  color: white;
  user-select: none;
  text-shadow: -1px -1px 0 #444, 1px -1px 0 #444, -1px 1px 0 #444, 1px 1px 0 #444;
}

.color-variant-panel {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 5px;
}

.variant-material-container {
  display: flex;
  flex-direction: column;
}

.variant-material-row {
  width: 100%;
  margin-top: 4px;
  margin-bottom: 4px;
}

.part-editor-icon {
  height: 24px;
  background-color: white;
  border-color: #444;
  margin: 5px;
}

.part-icon {
  @include no-user-select();
  @include light-shadow();
  width: auto;
  height: auto;
  margin-top: 5px;
  display: block; 
  margin: auto;
}

.component-parts-container {
  width: 100%;
}

.component-part-container {
  display: flex;
  padding: 5px;
  width: 100%;
  align-items: center;
}

.configurator-position-right {
  padding-top: 20px;
  background-color: white;
  width: 300px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  border-left: 1px solid #888;
}

.configurator-position-left {
  padding-top: 20px;
  background-color: white;
  width: 300px;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border-right: 1px solid #888;
}

.configurator-position-bottom {
  padding-top: 5px;
  background-color: white;
  width: 100%;
  height: fit-content;
  z-index: 10;
  border-top: 1px solid #888;
}

.configurator-position-offscreen {
  position: absolute;
  left: -1000px;
}

.configurator-panel-row {
  margin-top: 5px;
  margin-bottom: 5px; 
  width: 90%;
  white-space: nowrap;
  overflow-x: scroll;
  display: flex;
}

.configurator-panel-row > div {
  margin: auto;
  flex-wrap: nowrap;
  display: flex;
}

.configurator-panel-row::-webkit-scrollbar {
  display: none !important;
}

.configurator-panel-row {
  -ms-overflow-style: none !important;
  scrollbar-width: 0 !important;
}

