.flexybox {
    border: 1px solid $primarycol;
    left: 0;
    top: 0;
    position: absolute;
    width: auto;
    height: auto;
    touch-action: none;
    cursor: pointer;
} 

.flexybox-container {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.flexybox-resize {
    width: 20px;
    height: 20px;
    right: 0;
    bottom: 0;
    position: absolute;
    z-index: 2;
    background: url(data:image/png;base64,);
    background-size: 100% 100%;
}

.flexybox-header {
    position: absolute;
     width: inherit;
     height: 30px;
     background-color: #222;
     left: 0;
     top: -30px;
     z-index: 2;
     width: 100%;
 }
 
 .flexybox-content {
     width: 100%;
     height: 100%;
     position: relative;
     left: 0;
     touch-action: none;
 }
 
 .flexybox-close {
   display: flex;
   justify-content: flex-end;
 } 
 
 .flexybox-resize:hover {
     cursor: se-resize;
 }

 
 .flexybox-content textarea {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}