
.image-box {
    flex: 25%;
    max-width: 250px;
    padding: 0 4px;
}
  
.image-box-name {
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.image-box img {
    margin-top: 8px;
    vertical-align: middle;
    width: 100%;
 }
  